.greeting {
    position: relative;
    display: flex;
    align-items: flex-start;
    width: 300px;
}

.error {
    position: absolute;
    right: -100px;
    color: red;
    height: 20px;
    margin-left: 10px;
}

.input {
    display: block;
    padding: 5px;
    border: 4px solid #F1B720;
    border-radius: 5px;
    color: #333;
    transition: all 0.3s ease-out;
}

.input:hover {
    border-radius: 8px
}

.input:focus {
    outline: none;
    border-radius: 8px;
    border-color: #EBD292;
}

.errorInput {
    display: block;
    padding: 5px;
    border: 4px solid #ff0000;
    border-radius: 5px;
    color: #333;
    transition: all 0.3s ease-out;
    background-color: #eb9292;
}

.errorInput:hover {
    border-radius: 8px
}

.errorInput:focus {
    outline: none;
    border-radius: 8px;
    border-color: #eb9292;
}

.button {
    position: absolute;
    right: 40px;
    top: -11px;
    display: inline-block;
    margin: 10px;
    padding: 8px 15px;
    background: #B8ED01;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    transition: all 0.3s ease-out;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5),
    0 2px 2px rgba(0, 0, 0, 0.3),
    0 0 4px 1px rgba(0, 0, 0, 0.2);
    /* Font styles */
    text-decoration: none;
    text-shadow: 0 1px rgba(255, 255, 255, 0.7);
}

.button:hover {
    background: #C7FE0A
}

.button:disabled {
    background-color: dimgray;
}

.count {
    position: absolute;
    right: 10px;
    top: -7px;
    margin: 10px;
}